.activity__list {
  flex-direction: column;
  gap: 1rem;
  padding: 0;
  display: flex;
}

.activity__card {
  background: var(--white-light);
  border-radius: .5rem;
  align-items: flex-start;
  gap: 1rem;
  padding: 1rem;
  display: flex;
  box-shadow: 1px 1px 6px #0000001a;
}

.activity__card__icon {
  aspect-ratio: 1;
  background: var(--blue-light);
  color: var(--blue-primary);
  border-radius: 52px;
  width: 40px;
  height: 40px;
  padding: .5rem;
  overflow: hidden;
}

.activity__card--alert .activity__card__icon {
  background: var(--red-light);
  color: var(--red-primary);
}

.activity__card__content {
  flex-direction: column;
  gap: .5rem;
  width: 100%;
  font-size: 1rem;
  display: flex;
}

.activity__card__content__top {
  justify-content: space-between;
  display: flex;
}

.activity__card__content__top__left {
  flex-direction: column;
  display: flex;
}

.activity__card__content__top__left span:nth-child(2) {
  color: var(--blue-primary);
  font-size: .8rem;
}

.activity__card--alert .activity__card__content__top__left span:nth-child(2) {
  color: var(--red-primary);
}

.activity__card__content__top__right {
  color: var(--dark-light);
  font-size: .8rem;
}

.activity__card__content__bottom {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.activity__card__content__bottom span:first-child {
  color: var(--dark-light);
  align-items: center;
  font-size: .9rem;
  display: inline-flex;
}

.activity__card__content__bottom span:first-child svg {
  color: var(--dark-light);
  width: 1.5rem;
  height: 1.5rem;
}

.activity__card__content__bottom span:nth-child(2) {
  color: var(--dark-light);
  font-size: .8rem;
  font-weight: 500;
}

#app {
  padding: 0;
}

header {
  background: linear-gradient(65deg, var(--blue-primary) 0%, cyan 70%);
  color: var(--white-primary);
  border-radius: 1rem 1rem 0 0;
  flex-direction: column;
  padding: 3rem 1rem;
  display: flex;
}

.header__top {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.header__top svg {
  color: var(--white-primary);
  width: auto;
  height: 20px;
  margin-right: 1rem;
}

.header__top .header__help {
  margin-left: auto;
}

.header__detail {
  flex-direction: column;
  margin: 2rem 0 0;
  display: flex;
}

.header__detail span:first-child {
  margin-bottom: .5rem;
  font-size: 1.8rem;
  font-weight: 600;
}

.statistics, .activity {
  padding: 1rem;
}

